<template>
  <div id="single-blog">
    <h1>{{ blog.title }}</h1>
    <article>{{ blog.content }}</article>
    <p>作者：{{ blog.author }}</p>
    <p>分类：</p>
    <ul>
      <li v-for="(category, i) in blog.categories" :key="i">
        {{ category }}
      </li>
    </ul>
    <button @click="deleteSingleBlog()">删除</button>
    <router-link :to="'/edit/' + id">编辑</router-link>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    this.$http
      .get("https://vuedemo-ae9d1.firebaseio.com/posts/" + this.id + ".json")
      .then(function(data) {
        // console.log(data);
        return data.json();
      })
      .then(function(data) {
        this.blog = data;
      });
  },
  methods: {
    deleteSingleBlog() {
      this.$http
        .delete(
          "https://vuedemo-ae9d1.firebaseio.com/posts/" + this.id + ".json"
        )
        .then(response => {
          //转到根路径
          this.$router.push({ path: "/" });
        });
    }
  }
};
</script>

<style>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>



